					</el-table-column>
					<el-table-column label="姓名" sortable width="200px" prop="nickname">
					</el-table-column>
					<el-table-column label="机构" sortable width="200px" prop="deptName">
					</el-table-column>
					<el-table-column label="公司" sortable width="200px" prop="companyName">
					</el-table-column>
				</el-table>
				<el-pagination background class="pull-right" @size-change="sizeChange" @current-change="currentChange" :current-page="page.currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="page.pageSize" layout="total, sizes, prev, pager, next" :total="page.totalCount">
				</el-pagination>
				<span slot="footer" class="dialog-footer">
	    			<el-button type="primary" @click="dailogconfirm">确 定</el-button>
	    			<el-button @click="dialogVisible = false">取 消</el-button>
	  			</span>
			</el-dialog>
			<!-- 产品类别  -->
			<categorymask ref="categorychild" @categorydata="categorydata"></categorymask>
			<!-- 产品名称  -->
			<productmask ref="productchild" @appenddata="appenddata"></productmask>
			<!--受检企业-->
			<enterprisemask ref="enterprisechild" @appendnames="appendnames"></enterprisemask>
			<!--审批页面-->
			<approvalmask :approvingData="approvingData" ref="approvalChild" @detail="detailgetData"></approvalmask>
			<!--流程历史-->
			<flowhistorymask :approvingData="approvingData"  ref="flowhistoryChild" ></flowhistorymask>
			<!--流程地图-->
			<flowmapmask :approvingData="approvingData" ref="flowmapChild" ></flowmapmask>
			<!--当前责任人-->
			<vewPoplemask :approvingData="approvingData"  ref="vewPopleChild" ></vewPoplemask>
			<!-- 检测依据  -->
			<teststandardmask ref="standardchild" @testbasis="addbasis"></teststandardmask>
			<!-- 检测项目  -->
			<testprojectmask ref="projectchild" @testproject="addproject"></testprojectmask>
		</div>
	</div>
</template>

<script>
	import Config from '../../config.js'
	import categorymask from '../common/common_mask/categorylistmask.vue'//产品
	import productmask from '../common/common_mask/productlistmask.vue'//产品
	import enterprisemask from '../common/common_mask/enterprisemask.vue'//受检企业
	import approvalmask from '../workflow/approving.vue'//审批页面
	import flowhistorymask from '../workflow/flowhistory.vue'//流程历史
	import flowmapmask from '../workflow/flowmap.vue'//流程地图
	import vewPoplemask from '../workflow/vewPople.vue'//当前责任人
	import teststandardmask from '../common/common_mask/teststandardmask.vue'//检测依据
	import testprojectmask from '../common/common_mask/testprojectmask.vue'//检测依据
	export default {
		name: 'masks',
		components: {
			 approvalmask,
			 flowhistorymask,
			 flowmapmask,
			 vewPoplemask,
			 categorymask,
			 productmask,
			 enterprisemask,
			 teststandardmask,
			 testprojectmask
		},
		data() {
			 var validateItemleader = (rule, value, callback) => {//项目负责人
                if (this.dataInfo.P_LEADERDesc === undefined || this.dataInfo.P_LEADERDesc === '' || this.dataInfo.P_LEADERDesc === null) {
                    callback(new Error('必填'));
                }else {
                    callback();
                }
            };
			 var validateproname = (rule, value, callback) => {//受检产品名称
                if (this.dataInfo.ITEM_NAME === undefined || this.dataInfo.ITEM_NAME === '' || this.dataInfo.ITEM_NAME === null) {
                    callback(new Error('必填'));
                }else {
                    callback();
                }
            };
			 var validateVname = (rule, value, callback) => {//受检企业
                if (this.dataInfo.V_NAME === undefined || this.dataInfo.V_NAME === '' || this.dataInfo.V_NAME === null) {
                    callback(new Error('必填'));
                }else {
                    callback();
                }
			};
			 //金额验证
            var price=(rule, value, callback) => { 
				var exp = /^(-)?\d{1,3}(,\d{3})*(.\d+)?$/;
				if(value != '' && value!=undefined){
					if(exp.test(value)==false){ 
	                    callback(new Error('请输入数字'));
	              }else{
	                    callback();
	                }
				}else {
					callback();
				}
           };
			return {
				approvingData:{},//流程的数据
				loadSign:true,//加载
				commentArr:{},
				falg:false,//保存验证需要的
				basic_url: Config.dev_url,
				gridData: [], //彈出框的數據
				fileList: [],
				resourceCheckedKey: [], //通过接口获取的需要默认展示的数组 
				getCheckboxData: {},
				resourceProps: {
					children: "children",
					label: "fullname"
				},
				page: {
					currentPage: 1,
					pageSize: 20,
					totalCount: 0
				},
				searchList: {
					nickname: '',
					username: '',
				},
				value: '',
				selUser: [],
				edit: true, //禁填
				noedit: false,
				editSearch: '', //判斷項目負責人和接收人
				col_but1: true,
				col_but2: true,
				show: false,
				isok1: true,
				isok2: false,
				down: true,
				up: false,
				type: '',
				addtitle: true, //添加弹出框titile
				modifytitle: false, //修改弹出框titile
				viewtitle: false, //查看弹出框title
				modify: false,
				views: false,
				noviews: true, //保存的按钮
				approval:false,
				start:false,
				activeName: 'first', //tabs
				activeNames: ['1', '2', '3', '4', '5', '6', '7'], //手风琴数量
				labelPosition: 'right', //表格
				dialogVisible: false, //对话框
				dataInfo: { //添加数据库列表信息
					N_CODE: '',
					TYPE: '',
					XD_DATE: '',
					PRODUCT_TYPE:'',
					ITEM_NAME: '',
					ITEM_MODEL: '',
					V_NAME: '',
					VENDOR:'',
					CJDW: '',
					P_LEADER: '',
					TASKNUM: '',
					SOLUTION: '',
					COMPDATE: '',
					STATE: '1',
					STATEDesc: '草稿',
					ENTERBY: '',
					STATUS: '',
					WORK_NOTICE_CHECKBASISList: [],
					WORK_NOTICE_CHECKPROJECTList: []
				},
				rules: {
					TYPE:[{required: true, message: '请至少选择一个', trigger: 'change'}],
					TASKNUM:[{required: false, trigger: 'change', validator:this.Validators.isSpecificKey}],
					CJDW: [{required: true,trigger: 'change',message: '请选择',}], //承检单位
					P_LEADERDesc: [{required: true, trigger: 'blur', message: '请选择'}], //项目负责人
					ITEM_NAME: [{required: true, trigger: 'blur', message:'请选择'}], //受检产品名称
					ITEM_MODEL: [
						{required: true,trigger: 'blur',message: '必填'},
						{trigger: 'blur', validator:this.Validators.isSpecificKey}
					], //受检产品型号
					V_NAME: [{required: true,validator: validateVname}], //受检企业
					VENDOR: [{required: true,trigger: 'blur',message: '必填'}], //受检企业编号
					QUALITY: [{required: true,message: '必填'},{ type: 'number', message: '必须为数字值'}], //样品数量
					CHECTCOST:[{required: false,trigger: 'change',validator:price}], //检验检测费用
					XD_DATE: [{type: 'string',required: true,message: '请选择',trigger: 'change'}],//下达日期
					SOLUTION: [
						{required: true,trigger: 'blur',message: '必填',	},
						{trigger: 'blur',validator: this.Validators.isSpecificKey},
					],//抽样方案
					MEMO: [
						{required: false, trigger: 'blur', validator: this.Validators.isSpecificKey},
					],
					// REMARKS: [{required: true,trigger: 'blur',message: '必填',}],
				},
				//tree
				resourceData: [], //数组，我这里是通过接口获取数据
				selectData:[],//承检单位数据
				dataid:2,//修改和查看带过的id
				workNot:'workNot',//appname
				catenum:'',//产品类别作为参数传值给依据
				pronum:'',//产品作为参数传值给依据
				basisnum:'',////依据选中数据们字符串作为参数传值给项目
				PRODUCT_TYPE:'',//产品类别
				username:'',
			};
		},
		methods: {
			//表头居中
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD");
			},
			reset() {
				this.dataInfo = {
					N_CODE: '',
					TYPE: '',
					XD_DATE: '',
					ITEM_NAME: '',
					PRODUCT_TYPE:'',
					ITEM_MODEL: '',
					V_NAME: '',
					VENDOR:'',
					CJDW: '',
					P_LEADER: '',
					TASKNUM: '',
					SOLUTION: '',
					COMPDATE: '',
					STATE: '1',
					STATEDesc: '草稿',
					ENTERBY: '',
					STATUS: '',
					WORK_NOTICE_CHECKBASISList: [],
					WORK_NOTICE_CHECKPROJECTList: []
				}
				// this.$nextTick(() => {
				// 	this.$refs.form.resetFields();
				// });
				// this.$refs["dataInfo"].resetFields();
			},
			//机构值
			getCompany() {
				var type = "2";
				var url = this.basic_url + '/api-user/depts/treeByType';
				this.$axios.get(url, {
					params: {
						type: type
					},
				}).then((res) => {
					this.selectData = res.data;
				});
			},
			addcategory(){//产品类别
				this.$refs.categorychild.visible(this.dataInfo.CJDW);
			},
			addproduct(){//受检产品名称
				this.$refs.productchild.visible(this.catenum);
			},
			//单位
			addCompany(type){
				this.$refs.enterprisechild.visible(type);
			},                                                                                                                                                                                                                                                                                                                                                                                                                                                                           

			handleNodeClick(data) { //获取勾选树菜单节点
				//				console.log(data);
			},
			handleCheckChange(data, checked, indeterminate) {
				this.getCheckboxData = data;
			},
			getCheckedNodes() { //获取树菜单节点
				this.checkedNodes = this.$refs.tree.getCheckedNodes()
			},
			
			toNum(str) {
				return str.replace(/\,|\￥/g, "");
			},
			//金额两位小数点千位分隔符，四舍五入
			toPrice() {
				var money = document.getElementById("cost").value;
				var num = parseFloat(this.toNum(money)).toFixed(2).toString().split(".");
				num[0] = num[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)', 'ig'), "$1,");
				this.dataInfo.CHECTCOST = num.join(".");
			},

			//tabs
			handleClick(tab, event) {
//				console.log(tab, event);
			},
			iconOperation(row, column, cell, event) {
				if(column.property === "iconOperation") {
					row.isEditing = !row.isEditing
				}
			},
			//删除行
			deleteRow(index, rows) { //Table-操作列中的删除行
				rows.splice(index, 1);
			},
			sizeChange(val) {
				this.page.pageSize = val;
			},
			currentChange(val) {
				this.page.currentPage = val;
			},
			//新建行
			addfieldBasis() {
				var obj = {
					NUMBER: '1',
					S_NUM: '',
					S_NAME: '',
					S_ENGNAME: '',
					VERSION: '',
					STATUS: '1',
					isEditing: true
				};
				this.dataInfo.WORK_NOTICE_CHECKBASISList.push(obj);
			},
			addfieldProject() {
				var obj = {
					NUMBER: '1',
					P_NUM: '',
					P_DESC: '',
					REMARKS: '',
					VERSION: '',
					STATUS: '1',
					isEditing: true
				};
				this.dataInfo.WORK_NOTICE_CHECKPROJECTList.push(obj);
			},

			//刪除新建行
			delfieldBasis(item) {
				var index = this.dataInfo.WORK_NOTICE_CHECKBASISList.indexOf(item);
				if(index !== -1) {
					this.dataInfo.WORK_NOTICE_CHECKBASISList.splice(index, 1);
				}
			},
			//点击按钮显示弹窗
			visible() {
				this.reset();
				this.$axios.get(this.basic_url + '/api-user/users/currentMap', {}).then((res) => {
					this.dataInfo.DEPTID = res.data.deptId;
					this.dataInfo.ENTERBY = res.data.id;
					// this.dataInfo.ORGID = res.data.deptName
					var date = new Date();
					this.dataInfo.ENTERDATE = this.$moment(date).format("YYYY-MM-DD ");
				}).catch((err) => {
					this.$message({
						message: '网络错误，请重试',
						type: 'error'
					})
				})
				
				this.addtitle = true;
				this.modifytitle = false;
				this.viewtitle = false;
				this.views = false; //
				this.noviews = true;
				this.edit = true;
				this.noedit = false;
			},
			detailgetData() {
				var url = this.basic_url +'/api-apps/app/workNot/' + this.dataid;
				this.$axios.get(url, {}).then((res) => {
					//依据对号控制
					for(var i = 0; i<res.data.WORK_NOTICE_CHECKBASISList.length;i++){
						res.data.WORK_NOTICE_CHECKBASISList[i].isEditing = false;
					}
					//项目要求对号控制
					for(var j = 0; j<res.data.WORK_NOTICE_CHECKPROJECTList.length;j++){
						res.data.WORK_NOTICE_CHECKPROJECTList[j].isEditing = false;
					}
					this.dataInfo = res.data; 
					this.dataInfo.CJDW=this.dataInfo.CJDWDesc;
					this.show = true;
				}).catch((err) => {
					this.$message({
						message: '网络错误，请重试',
						type: 'error'
					});
				});
			},	
			// 这里是修改
			detail(dataid) {
				this.dataid=dataid;
				var usersUrl = this.basic_url + '/api-user/users/currentMap'
				this.$axios.get(usersUrl, {}).then((res) => {
					this.dataInfo.DEPTID = res.data.deptId;//传给后台机构id
					this.dataInfo.CHANGEBY = res.data.id;
					var date = new Date();
					this.dataInfo.CHANGEDATE = this.$moment(date).format("yyyy-MM-dd hh:mm:ss");
				}).catch((err) => {
					this.$message({
						message: '网络错误，请重试1',
						type: 'error'
					});
				});
				this.detailgetData();
				this.addtitle = false;
				this.modifytitle = true;
				this.viewtitle = false;
				this.noviews = true;
				this.views = false; //
				this.edit = true;
				this.noedit = false;
			},
			//这是查看
			view(dataid) {
				this.dataid=dataid;
				this.addtitle = false;
				this.modifytitle = false;
				this.viewtitle = true;
				this.views = true; //
				this.noviews = false;
				this.edit = true;
				this.noedit = true;
				this.detailgetData();
				this.isEditing=false;
				//判断启动流程和审批的按钮是否显示
				var url = this.basic_url + '/api-apps/app/workNot/flow/isStart/'+dataid;
					this.$axios.get(url, {}).then((res) => {
					  if(res.data.resp_code==1){
							this.start=true;
							this.approval=false;
						}else{
						var url = this.basic_url + '/api-apps/app/workNot/flow/Executors/'+dataid;
						this.$axios.get(url, {}).then((res) => {
							var resullt=res.data.datas;
							var users='';
							for(var i=0;i<resullt.length;i++){
								users = users + resullt[i].username+",";
							}
							if(users.indexOf(this.username) != -1){
								this.approval=true;
								this.start=false;
							}
						});
						}
					});
			},
			//上传文件 Begin
			handleRemove(file, fileList) {
//				console.log(file, fileList);
			},
			handlePreview(file) {
//				console.log(file);
			},
			handleExceed(files, fileList) {
				this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
			},
			beforeRemove(file, fileList) {
				return this.$confirm(`确定移除 ${ file.name }？`);
			},
			//上传文件 End
			//点击关闭按钮
			close() {
				this.show = false;
				this.$emit('request');
			},
			open(){
				this.show = true;
			},
			toggle(e) {
				if(this.isok1 == true) {
					this.maxDialog();
				} else {
					this.rebackDialog();
				}
			},
			maxDialog(e) { //定义大弹出框一个默认大小
				this.isok1 = false;
				this.isok2 = true;
				$(".mask_div").width(document.body.clientWidth);
				$(".mask_div").height(document.body.clientHeight - 60);
				$(".mask_div").css("top", "60px");
			},
			//还原按钮
			rebackDialog() { //大弹出框还原成默认大小
				this.isok1 = true;
				this.isok2 = false;
				$(".mask_div").css("width", "80%");
				$(".mask_div").css("height", "80%");
				$(".mask_div").css("top", "100px");
			},
			//接到产品类别的值
			categorydata(value){
				this.catenum = value[0];
				this.dataInfo.PRODUCT_TYPE = value[1];
			},
			//接到产品的值
			appenddata(value){
				this.pronum = value[0];
				this.dataInfo.ITEM_NAME = value[1];
			},
			appendnames(value){
				this.dataInfo.V_NAME = value;//名称
			},
			
			// 保存users/saveOrUpdate
			save() {
				this.$refs.dataInfo.validate((valid) => {
		          if (valid) {
							if(this.dataInfo.WORK_NOTICE_CHECKBASISList.length<=0&&this.dataInfo.WORK_NOTICE_CHECKPROJECTList.length<=0){
			        		this.$message({
								message: '依据和检验检测项目是必填项，请填写！',
								type: 'warning'
						    });
						return false;
			      }else{
			         var oDate1 = new Date(this.dataInfo.XD_DATE); //下达日期
    				 var oDate2 = new Date(this.dataInfo.COMPDATE);//完成日期
    				  if(oDate1.getTime() > oDate2.getTime()){ 
        						this.$message({
								message: '完成时间不能早于下达时间',
								type: 'warning'
							}); 
						return;	
    				  }
			        if(typeof(this.dataInfo.CJDW) != 'undefind') {
			        	 for(var j=0;j<this.selectData.length;j++){
			        	 	if(this.dataInfo.CJDW==this.selectData[j].fullname){
			        	 		this.dataInfo.CJDW=this.selectData[j].id
			        	 	}
			        	 }		
			        }
					var url = this.basic_url + '/api-apps/app/workNot/saveOrUpdate';
					this.$axios.post(url, this.dataInfo).then((res) => {
						if(res.data.resp_code == 0) {
							this.$message({
								message: '保存成功',
								type: 'success'
							});
							//重新加载数据
							this.$emit('request');
							this.reset();
						}
						this.falg=true
					}).catch((err) => {
						this.$message({
							message: '网络错误，请重试',
							type: 'error'
						});
						this.falg=false
					});}
					this.falg=true
					} else {
						this.show=true;
					 	this.$message({
							message: '有必填项未填写，请重新填写',
							type: 'warning',
						});
						this.falg=false
					}
				});
			},
			//保存
			saveAndUpdate() {
				this.save();
				if(this.falg){
					this.show = false;
				}
				
			},
			//提交并保存
			saveAndSubmit() {
				this.save();
				this.show = true;
			},
			handleClose(done) {
				this.$confirm('确认关闭？')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			searchinfo() {
				this.page.currentPage = 1;
				this.page.pageSize = 20;
				this.addperbtn();
			},
			//项目负责人放大镜
			addperbtn(type){
				if(this.dataInfo.CJDW==""||this.dataInfo.CJDW=="undefined"){
					this.$message({
						message: '请先选择承检单位',
						type: 'warning'
						});
				}else{
					var params = {
					page: this.page.currentPage,
					limit: this.page.pageSize,
					deptId: this.searchList.deptId,
					nickname: this.searchList.nickname,
					username: this.searchList.username,
				}
				var url = this.basic_url + '/api-user/users/usersByDept?deptId='+this.dataInfo.CJDW;
				this.$axios.get(url, {
					params: params
				}).then((res) => {
					this.gridData = res.data.data;
				});
				this.dialogVisible = true;	
				this.type=type;
				}
			},
			dailogconfirm() { //选择人员确定按钮
				if(this.selUser.length == 0){
					this.$message({
						message: '请选择数据',
						type: 'warning'
					});
				}else if(this.selUser.length > 1){
					this.$message({
						message: '不可同时选择多条数据',
						type: 'warning'
					});
				}else{
					this.dialogVisible = false;
					if(this.type == 'leader') {
						this.dataInfo.P_LEADER = this.selUser[0].id;
						this.dataInfo.P_LEADERDesc = this.selUser[0].nickname;
					} else {
						this.dataInfo.ACCEPT_PERSON = this.selUser[0].id;
						this.dataInfo.ACCEPT_PERSONDesc = this.selUser[0].nickname;
					}
				}
			},
			//生成委托书
			build(){
				var dataid = this.dataInfo.ID;
				    var url=this.basic_url + '/api-apps/app/workNot/operate/createInspectProxy?ID=' + dataid;
					this.$axios.get(url, {}).then((res) => {
						if(res.data.resp_code == 0) {
							this.$message({
								message: '生成委托书成功',
								type: 'success'
							});
						}else{
							this.$message({
							message: '已经生成委托书，请勿重复生成',
							type: 'warning'
							});
						}
					}).catch((err) => {
						this.$message({
							message: '网络错误，请重试',
							type: 'error'
						});
					});
			},
			SelChange(val) {
				this.selUser = val;
			},
			loadMore () {
			   if (this.loadSign) {
			     this.loadSign = false
			     this.page.currentPage++
			     if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
			       return
			     }
			     setTimeout(() => {
			       this.loadSign = true
			     }, 1000)
			     this.requestData()
			   }
			 },
			 //检测依据列表
			addbasis(value){
				this.basisnum = value[0];
				for(var i = 1;i<value.length;i++){
					this.dataInfo.WORK_NOTICE_CHECKBASISList.push(value[i]);
				}
			},
			 //检测项目列表
			addproject(value){
				for(var i = 0;i<value.length;i++){
					value[i].P_DESC = value[i].P_NAME;
					this.dataInfo.WORK_NOTICE_CHECKPROJECTList.push(value[i]);
				}
			},
			 //启动流程
			startup(){
				var url = this.basic_url + '/api-apps/app/workNot/flow/'+this.dataid;
				this.$axios.get(url, {}).then((res) => {
					if(res.data.resp_code == 1) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
				    }else{
				    	this.$message({
								message:res.data.resp_msg,
								type: 'success'
							});
						var url = this.basic_url + '/api-apps/app/workNot/flow/Executors/'+this.dataid;
						this.$axios.get(url, {}).then((res) => {
								var resullt=res.data.datas;
								var users='';
								for(var i=0;i<resullt.length;i++){
								users = users + resullt[i].username+",";
							}
								if(users.indexOf(this.username) != -1){
									this.approval=true;
									this.start=false;
								}
								this.detailgetData();
						});
							
				    }
				});
			},
			//审批流程
			approvals(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
				var url = this.basic_url + '/api-apps/app/workNot/flow/isEnd/'+this.dataid;
		    		this.$axios.get(url, {}).then((res) => {
		    			if(res.data.resp_code == 0) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
		    			}else{
		    				var url = this.basic_url + '/api-apps/app/workNot/flow/isExecute/'+this.dataid;
		    				this.$axios.get(url, {}).then((res) => {
				    			if(res.data.resp_code == 1) {
										this.$message({
											message:res.data.resp_msg,
											type: 'warning'
										});
								}else{
									this.$refs.approvalChild.visible();
								}
		    		});
		    		}
				});
			},
			//流程历史
			flowhistory(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
//				this.$refs.flowhistoryChild.open();
				this.$refs.flowhistoryChild.getdata(this.dataid);
			},
			//流程地图
			flowmap(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
				this.$refs.flowmapChild.getimage(this.dataid);
			},
			//当前责任人
			viewpepole(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
				this.$refs.vewPopleChild.getvewPople(this.dataid);
			},
			//检测依据放大镜
			basisleadbtn(){
				this.$refs.standardchild.basislead(this.pronum);
			},
			//检测项目放大镜
			basisleadbtn2(){
				this.$refs.projectchild.projectlead(this.basisnum);
			},
		    getuser(){//获取当前用户信息
	            var url = this.basic_url + '/api-user/users/currentMap';
	            this.$axios.get(url, {}).then((res) => {//获取当前用户信息
	                    this.username = res.data.username;
	            }).catch((err) => {
	                this.$message({
	                    message: '网络错误，请重试',
	                    type: 'error'
	                });
	            });
        	},
		},
		mounted() {
			this.getCompany();
			this.getuser();
		},
	}
</script>

<style>
	@import '../../assets/css/mask-modules.css';
	.el-radio-group .el-col-4 {
		padding-top: 5px;
	}
	
	#cost {
		text-align: right !important;
	}
</style>


